













import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Radio extends Vue {
  @Prop()
  label!: String;

  @Prop()
  name!: String;

  @Prop()
  value!: Boolean;

  onLabelClick(): void {
    let input: any = this.$refs[`${this.label}`];
    input.checked = true;
  }
}
