












import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Checkbox extends Vue {
  @Prop()
  label!: String;

  @Prop()
  value!: Boolean;

  onLabelClick() {
    let input: any = this.$refs[`${this.label}`];
    input.checked = !input.checked;
  }
}
