


























import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Button extends Vue {
  @Prop()
  onClick!: {
    type: Function;
  };

  @Prop()
  icon!: {
    type: String;
    default: null;
  };

  @Prop()
  title!: {
    type: String;
    default: null;
  };

  @Prop()
  price!: {
    type: String;
    default: null;
  };

  @Prop()
  description!: {
    type: String;
    default: null;
  };

  @Prop()
  href!: {
    type: String;
    default: null;
  };

  // standard, medium, large, square
  @Prop()
  size!: {
    type: String;
    size: null;
  };

  // hover, focus, loading, disabled
  @Prop()
  state!: {
    type: String;
    default: null;
  };

  // set buttons type to "submit"
  @Prop()
  type!: {
    type: String;
    default: null;
  };

  @Prop()
  to!: {
    type: String;
    default: null;
  };

  // button, a, router-link
  @Prop({ default: "button" })
  tag!: String;

  @Prop()
  variation!: {
    type: String;
    default: "default";
  };

  get buttonClasses() {
    let classes = [];

    if (this.variation) {
      classes.push(`button--${this.variation}`);
    }

    if (this.variation) {
      classes.push(`button--${this.variation}`);
    }

    if (this.size) {
      classes.push(`button--${this.size}`);
    }

    if (this.state) {
      classes.push(`is-${this.state}`);
    }

    return classes.join(" ");
  }

  get iconClass() {
    let classes = [];

    if (this.icon) {
      classes.push(`icon-${this.icon}`);
    }

    return classes.join(" ");
  }
}
